.card{
    position: absolute;
    background-color: grey;
    width: 20%;
    height: 75vh;
    top: 10%;
    text-align: center;
    padding: 1%;
    border-radius: 2%;
    transition: 0.3s;
    opacity: 0.9;
    border: 1vh solid;
    border-color: rgb(39, 39, 39);
}

h1{
    font-size: 5vh; /* Die Schriftgröße bleibt relativ */
}

.card:hover{
    margin-left: 2%;
}

svg{
    fill: rgb(97, 97, 97);
    stroke: rgb(45, 45, 45);
    stroke-width: 0.25%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.zoomed {
    transform: scale(2); /* Zum Beispiel: Hier wird die Karte auf das Doppelte vergrößert */
    transform-origin: center; /* Der Mittelpunkt des Zooms */
}